import { lazy, useState } from "react";
import style from "../LeavesAndTransfer.module.css";
import { useStaffLeaveAction } from "../../../../../hooks/institute/institute-staff-student-api";
import { useTranslation } from "react-i18next";
import QLoader from "../../../../../Loader/QLoader";
import CustomSuspense from "../../../../../Custom/SuspenseLoading/CustomSuspense";
import { imageShowUrl, pdfShowUrlNext } from "../../../../../services/BaseUrl";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import moment from "moment";
const LeavesModal = lazy(() =>
  import(
    "../../../../../StaffTab/components/DividerTab/MainContent/StaffLeavesandTransfer/LeavesTab/Leaves/LeavesModal/LeavesModal"
  )
);

const RecomendingLeaveRequestCard = ({
  leave,
  onStaffLeaveListRefetch,
  isView,
  modId,
  flow,
}) => {
  const { t } = useTranslation();
  const [staffLeaveAction] = useStaffLeaveAction();
  const [disabled, setDisabled] = useState(false);
  const [view, setView] = useState(false);

  const onLeaveAction = (query) => {
    if (modId && flow) {
      setDisabled((pre) => !pre);
      staffLeaveAction({
        id: leave?._id,
        leaveAction: {
          leave_from: flow,
          staff_mod: modId,
          status: query,
        },
      }).then(() => {
        onStaffLeaveListRefetch();
        setDisabled((pre) => !pre);
      });
    }
  };
  return (
    <>
      <div className={style.request_card}>
        <div className={style.request_card_header}>
          <div className={style.request_card_text}>
            <img
              src={
                leave?.staff?.staffProfilePhoto
                  ? `${imageShowUrl}/${leave?.staff?.staffProfilePhoto}`
                  : "/images/demo_users/user5.svg"
              }
              alt="icon"
              style={{
                width: "3.5rem",
                height: "3.5rem",
              }}
            />
            <div className={style.request_card_inner_text}>
              <h6>{`${leave?.staff?.staffFirstName} ${
                leave?.staff?.staffMiddleName
                  ? leave?.staff?.staffMiddleName
                  : ""
              } ${leave?.staff?.staffLastName}`}</h6>
              <p>
                {isView === "Sactioning" ? (
                  <>
                    {t("reviewed_on")}{" "}
                    {moment(leave?.review?.review_on).format("LT")},{" "}
                    {moment(leave?.review?.review_on).format("DD MMM yyyy")}
                  </>
                ) : isView === "Reviewing" ? (
                  <>
                    {t("recomend_on")}
                    {moment(leave?.recommend?.recommend_on).format("LT")},{" "}
                    {moment(leave?.recommend?.recommend_on).format(
                      "DD MMM yyyy"
                    )}
                  </>
                ) : (
                  <>
                    {t("raised_on")} {moment(leave?.createdAt).format("LT")},{" "}
                    {moment(leave?.createdAt).format("DD MMM yyyy")}
                  </>
                )}

                <span className={style.viewAll} onClick={() => setView(true)}>
                  {t("for_dates")}
                </span>
              </p>
              <p>
                {isView === "Sactioning" ? (
                  <>
                    {t("reviewing_by")}
                    {`${leave?.review?.review_by?.staffFirstName ?? ""} ${
                      leave?.review?.review_by?.staffMiddleName ?? ""
                    } ${leave?.review?.review_by?.staffLastName ?? ""}`}
                  </>
                ) : isView === "Reviewing" ? (
                  <>
                    {t("recomending_by")}
                    {`${leave?.recommend?.recommend_by?.staffFirstName ?? ""} ${
                      leave?.recommend?.recommend_by?.staffMiddleName ?? ""
                    } ${leave?.recommend?.recommend_by?.staffLastName ?? ""}`}
                  </>
                ) : (
                  <>
                    {t("raised_on")} {moment(leave?.createdAt).format("LT")},{" "}
                    {moment(leave?.createdAt).format("DD MMM yyyy")}
                  </>
                )}
              </p>
            </div>
          </div>
          <div className={style.request_card_btn_group}>
            {leave?.status === "Request" ? (
              <>
                <div
                  className={style.reject_btn}
                  onClick={() => {
                    onLeaveAction("Rejected");
                  }}
                >
                  {t("reject")}
                </div>

                <div
                  className={style.accept_btn}
                  onClick={() => {
                    onLeaveAction("Accepted");
                  }}
                >
                  {t("approve")}
                </div>
              </>
            ) : null}
          </div>
        </div>
        <div
          className={style.request_card_inner_text}
          style={{
            marginTop: "0.5rem",
          }}
        >
          <h6
            style={{
              fontWeight: "400",
            }}
          >
            {t("leave_type")} {" : "}
            {leave?.leave_type}
          </h6>
        </div>
        <div
          className={style.request_card_inner_text}
          style={{
            marginTop: "0.5rem",
          }}
        >
          <h6
            style={{
              fontWeight: "400",
              whiteSpace: "break-spaces",
            }}
          >
            {t("reason")} {" : "}
            {leave?.reason}
          </h6>
        </div>
        {leave?.attach && (
          <div
            className={style.request_card_inner_text}
            style={{
              marginTop: "0.5rem",
            }}
          >
            <h6
              style={{
                fontWeight: "400",
                whiteSpace: "break-spaces",
              }}
            >
              {t("attachment")} {" : "}
              <a
                href={`${pdfShowUrlNext}/${leave?.attach}`}
                target="_blank"
                rel="noopener noreferrer"
                style={{
                  textDecoration: "none",
                }}
              >
                <span className={style.viewAll}>{t("view_attachment")}</span>
              </a>
            </h6>
          </div>
        )}
      </div>
      <BorderBottom
        customStyle={{
          width: "100%",
        }}
      />
      {view && (
        <CustomSuspense>
          <LeavesModal
            open={view}
            hideModal={(w) => setView(w)}
            dates={leave?.date}
          />
        </CustomSuspense>
      )}
      {disabled && <QLoader />}
    </>
  );
};

export default RecomendingLeaveRequestCard;
