import React, { useEffect, useState } from "react";
import style from "../../Hostel/Hostel.module.css";
import { useInView } from "react-intersection-observer";
import { useTranslation } from "react-i18next";
import QvipleLoading from "../../../../../Loader/QvipleLoading";
import AssignStaffModal from "../../../../../Department/Institute/AssignStaff/AssignStaffModal";
import QLoader from "../../../../../Loader/QLoader";
import RecomendingStaffListCard from "../Recomending/RecomendingStaffListCard";
import {
  useLatAuthorityGetAllStaff,
  useLatStaffAdd,
} from "../../../../../hooks/member_tab/department-api";
const ReviewingStaffList = ({ carryParentState, instituteId, modId }) => {
  const { t } = useTranslation();
  const [addStaff, setAddStaff] = useState(false);
  const [ref, inView] = useInView();
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [showingDataLoading, setShowingDataLoading] = useState(true);
  const [moderatorList, setModeratorList] = useState([]);
  const [staffListId, setStaffListId] = useState([]);
  const [staffListId1, setStaffListId1] = useState([]);
  const [disabled, setDisabled] = useState(false);

  const [latStaffAdd] = useLatStaffAdd();
  const { latAuthorityGetAllStaff, latAuthorityGetAllStaffRefetch } =
    useLatAuthorityGetAllStaff({
      data: {
        mid: modId,
        page: page,
        limit: 10,
        flow: "Review_Section",
      },
      skip: !modId,
    });

  useEffect(() => {
    if (modId) {
      setShowingDataLoading(true);
      latAuthorityGetAllStaffRefetch();
    }
  }, [modId, page, latAuthorityGetAllStaffRefetch]);
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);

  useEffect(() => {
    if (latAuthorityGetAllStaff?.all_staff) {
      if (page === 1) {
        setModeratorList(latAuthorityGetAllStaff?.all_staff);
        let arr = [];
        for (let st of latAuthorityGetAllStaff?.all_staff) {
          arr.push(st?._id);
        }
        setStaffListId1(arr);
      } else {
        setModeratorList((prevState) =>
          [
            ...new Set(
              [...prevState, ...latAuthorityGetAllStaff?.all_staff]?.map(
                JSON.stringify
              )
            ),
          ]?.map(JSON.parse)
        );
        let arr = [];
        for (let st of latAuthorityGetAllStaff?.all_staff) {
          arr.push(st?._id);
        }
        setStaffListId1(arr);
      }

      setShowingDataLoading(false);
    } else if (latAuthorityGetAllStaff?.all_staff?.length === 0)
      setShowingDataLoading(false);
    else {
    }

    if (latAuthorityGetAllStaff?.all_staff?.length === 10) setState(true);
    else setState(false);
  }, [latAuthorityGetAllStaff?.all_staff]);

  const onClose = () => {
    setAddStaff((pre) => !pre);
  };
  const onRefetchWhenAdd = () => {
    setPage((prev) => (prev = 1));
    setShowingDataLoading(true);
    latAuthorityGetAllStaffRefetch();
  };

  const onSelect = (val) => {
    // console.info("sjdfsvdjfsd", val);
    if (staffListId?.includes(val?._id))
      setStaffListId((prev) => prev?.filter((id) => id !== val?._id));
    else setStaffListId((prev) => [...prev, val?._id]);
  };
  const onAddStaff = () => {
    if (modId) {
      onClose();
      setDisabled((pre) => !pre);
      latStaffAdd({
        mid: modId,
        latBody: {
          flow: "Review_Section",
          staff_arr: staffListId,
        },
      })
        .then(() => {
          onRefetchWhenAdd();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  return (
    <>
      <div
        className={style.with_search}
        style={{
          justifyContent: "flex-end",
          marginTop: "0.8rem",
        }}
      >
        <p className={style.add_new_category} onClick={onClose}>
          {t("add_new_staff")}
        </p>
      </div>
      {moderatorList?.map((staff, index) =>
        moderatorList?.length === index + 1 ? (
          <div ref={ref} key={staff?._id}>
            <RecomendingStaffListCard
              staff={staff}
              carryParentState={carryParentState}
              onRefetch={onRefetchWhenAdd}
              viewAs="Reviewing"
              instituteId={instituteId}
              modId={modId}
              flow="Review_Section"
            />
          </div>
        ) : (
          <RecomendingStaffListCard
            staff={staff}
            key={staff?._id}
            carryParentState={carryParentState}
            onRefetch={onRefetchWhenAdd}
            viewAs="Reviewing"
            instituteId={instituteId}
            modId={modId}
            flow="Review_Section"
          />
        )
      )}

      {addStaff && (
        <AssignStaffModal
          assignHead={addStaff}
          title={t("add_staff")}
          onClose={onClose}
          instituteId={instituteId}
          viewAs="ADMISSION_ADMIN"
          isMultiSelect
          onMultiSelect={onSelect}
          selectList={[...staffListId, ...staffListId1]}
          onDone={onAddStaff}
        />
      )}
      {disabled && <QLoader />}
      {showingDataLoading && <QvipleLoading />}
    </>
  );
};

export default ReviewingStaffList;
