import { useLocation } from "react-router-dom";
import StaffMemberConfig from "../Mainbody/StaffMemberConfig";
import LeaveAndTransferAuthority from "../Mainbody/LeaveAndTransferAuthority/LeaveAndTransferAuthority";
import Recomending from "../Mainbody/LeaveAndTransferAuthority/Recomending/Recomending";
import Reveiwing from "../Mainbody/LeaveAndTransferAuthority/Reveiwing/Reveiwing";
import Sactioning from "../Mainbody/LeaveAndTransferAuthority/Sactioning/Sactioning";

const LeaveAndTransferConfig = () => {
  const location = useLocation();
  // console.info("sakjfbnsdfs", location.state);
  return (
    <StaffMemberConfig
      index="26"
      sid={location?.state?.sid ?? ""}
      onClickValue={location?.state?.onClickValue ?? ""}
    >
      {location.state?.subRole === "LEAVING_AND_TRANSFER_ACCESS" ? (
        <LeaveAndTransferAuthority
          instituteId={location?.state?.onClickValue?.institute?._id}
          carryParentState={location?.state}
        />
      ) : location.state?.subRole === "LEAVE_RECOMMENDATION_ACCESS" ? (
        <Recomending
          instituteId={location?.state?.onClickValue?.institute?._id}
          carryParentState={location?.state}
          modId={location?.state?.onClickValue?._id}
        />
      ) : location.state?.subRole === "LEAVE_REVIEW_ACCESS" ? (
        <Reveiwing
          instituteId={location?.state?.onClickValue?.institute?._id}
          carryParentState={location?.state}
          modId={location?.state?.onClickValue?._id}
        />
      ) : location.state?.subRole === "LEAVE_SANCTION_ACCESS" ? (
        <Sactioning
          instituteId={location?.state?.onClickValue?.institute?._id}
          carryParentState={location?.state}
          modId={location?.state?.onClickValue?._id}
        />
      ) : null}
    </StaffMemberConfig>
  );
};

export default LeaveAndTransferConfig;
