import { useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import QvipleLoading from "../../../../../Loader/QvipleLoading";
import RecomendingLeaveHistoryCard from "../Recomending/RecomendingLeaveHistoryCard";
import { useLatAuthorityGetAllHistory } from "../../../../../hooks/member_tab/department-api";

const SactioningLeaveHistory = ({ modId }) => {
  const [ref, inView] = useInView();
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [showingDataLoading, setShowingDataLoading] = useState(true);
  const [leaveList, setLeaveList] = useState([]);

  const { latAuthorityGetAllHistory, latAuthorityGetAllHistoryRefetch } =
    useLatAuthorityGetAllHistory({
      data: {
        mid: modId,
        page: page,
        limit: 10,
        flow: "Sanction_Section",
      },
      skip: !modId,
    });
  useEffect(() => {
    if (modId) {
      setShowingDataLoading(true);
      latAuthorityGetAllHistoryRefetch();
    }
  }, [modId, page, latAuthorityGetAllHistoryRefetch]);
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);

  useEffect(() => {
    if (latAuthorityGetAllHistory?.all_leave) {
      if (page === 1) {
        setLeaveList(latAuthorityGetAllHistory?.all_leave);
        setShowingDataLoading(false);
      } else {
        setLeaveList((prevState) =>
          [
            ...new Set(
              [...prevState, ...latAuthorityGetAllHistory?.all_leave]?.map(
                JSON.stringify
              )
            ),
          ]?.map(JSON.parse)
        );
        setShowingDataLoading(false);
      }
    } else if (latAuthorityGetAllHistory?.all_leave?.length === 0)
      setShowingDataLoading(false);
    else {
    }
    if (latAuthorityGetAllHistory?.all_leave?.length === 10) setState(true);
    else setState(false);
  }, [latAuthorityGetAllHistory?.all_leave]);

  return (
    <>
      {leaveList?.map((leave, index) =>
        leaveList?.length === index + 1 ? (
          <div ref={ref} key={leave?._id}>
            <RecomendingLeaveHistoryCard leave={leave} viewBy="Sactioning" />
          </div>
        ) : (
          <RecomendingLeaveHistoryCard
            key={index}
            leave={leave}
            viewBy="Sactioning"
          />
        )
      )}
      {showingDataLoading && <QvipleLoading />}
    </>
  );
};

export default SactioningLeaveHistory;
