import React from "react";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import style from "../../PromoteDepartment/PromoteDepartment.module.css";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import UniversalTab from "../../DepartmentHead/Competetions/CompetitionTab/UniversalTab";
import ReviewingLeaveRequest from "./ReviewingLeaveRequest";
import ReviewingLeaveHistory from "./ReviewingLeaveHistory";
import ReviewingStaffList from "./ReviewingStaffList";
const Reveiwing = ({ carryParentState, modId, instituteId }) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const params = useParams();
  const tabList = [
    {
      name: t("leave_request"),
      id: 0,
      url: "member/leavestransfer",
      is_active: "enabled",
    },
    {
      name: t("history"),
      id: 1,
      url: "member/leavestransfer?a=history",
      is_active: "enabled",
    },
  ];
  const onNavigate = () => {
    navigate(`/q/${params.username}/member/leavestransfer?a=staff`, {
      state: carryParentState,
    });
  };
  return (
    <div className={style.departmenthead}>
      <div className={style.demaprtment_list}>
        <div className={style.with_search}>
          <h6 className={style.department_card_name}>
            {t("reviewing_authority")}
          </h6>
          <img
            src="/images/member_tab/department/department-staff-icon.svg"
            alt="Class Catalog"
            className={style.rec_icon}
            onClick={onNavigate}
          />
        </div>
        <BorderBottom
          customStyle={{
            width: "100%",
            marginTop: "0.5rem",
          }}
        />
        {searchParams.get("a") === "staff" ? null : (
          <>
            <UniversalTab
              tabList={tabList}
              activeIndexTab={!searchParams.get("a") ? 0 : 1}
              showAs={true}
              customStyleTab={{
                width: "100%",
              }}
              customStyleLink={{
                width: "50%",
              }}
              carryParentState={carryParentState}
            />
            <BorderBottom
              customStyle={{
                width: "100%",
              }}
            />
          </>
        )}
        {!searchParams.get("a") ? (
          <ReviewingLeaveRequest modId={modId} />
        ) : searchParams.get("a") === "history" ? (
          <ReviewingLeaveHistory modId={modId} />
        ) : searchParams.get("a") === "staff" ? (
          <ReviewingStaffList
            modId={modId}
            instituteId={instituteId}
            carryParentState={carryParentState}
          />
        ) : null}
      </div>
    </div>
  );
};

export default Reveiwing;
