import { useState } from "react";
import QLoader from "../../../../../Loader/QLoader";
import Overlay from "../../../../../Search/Overlay";
import SearchModalBackdrop from "../../../../../Search/SearchModalBackdrop";
import style from "../../ClassTeacher/Profile/EditConfirm.module.css";
import { useLatStaffRemove } from "../../../../../hooks/member_tab/department-api";
const RecomendingStaffListCardMenu = ({
  onClose,
  onRefetch,
  staff,
  flow,
  modId,
}) => {
  const [disabled, setDisabled] = useState(false);
  const [latStaffRemove] = useLatStaffRemove();

  const onDelete = () => {
    if (modId && staff?._id && flow) {
      setDisabled((pre) => !pre);
      latStaffRemove({
        mid: modId,
        latBody: {
          flow: flow,
          staff_arr: [staff?._id],
        },
      })
        .then(() => {
          onRefetch();
          onClose();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  return (
    <>
      <SearchModalBackdrop onClose={onClose} />
      <Overlay
        customStyle={{
          zIndex: "1200",
        }}
      >
        <div className={style.edit_modal_container} style={{ width: "200px" }}>
          <h6 className={style.remove} onClick={onDelete}>
            Remove
          </h6>
        </div>
        {disabled && <QLoader />}
      </Overlay>
    </>
  );
};

export default RecomendingStaffListCardMenu;
