import { useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import QvipleLoading from "../../../../../Loader/QvipleLoading";
import RecomendingLeaveRequestCard from "./RecomendingLeaveRequestCard";
import { useLatAuthorityGetAllRequest } from "../../../../../hooks/member_tab/department-api";

const RecomendingLeaveRequest = ({ modId }) => {
  const [ref, inView] = useInView();
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [showingDataLoading, setShowingDataLoading] = useState(true);
  const [leaveList, setLeaveList] = useState([]);

  const { latAuthorityGetAllRequest, latAuthorityGetAllRequestRefetch } =
    useLatAuthorityGetAllRequest({
      data: {
        mid: modId,
        page: page,
        limit: 10,
        flow: "Recommend_Section",
      },
      skip: !modId,
    });
  useEffect(() => {
    if (modId) {
      setShowingDataLoading(true);
      latAuthorityGetAllRequestRefetch();
    }
  }, [modId, page, latAuthorityGetAllRequestRefetch]);
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);

  useEffect(() => {
    if (latAuthorityGetAllRequest?.all_leave) {
      if (page === 1) {
        setLeaveList(latAuthorityGetAllRequest?.all_leave);
        setShowingDataLoading(false);
      } else {
        setLeaveList((prevState) =>
          [
            ...new Set(
              [...prevState, ...latAuthorityGetAllRequest?.all_leave]?.map(
                JSON.stringify
              )
            ),
          ]?.map(JSON.parse)
        );
        setShowingDataLoading(false);
      }
    } else if (latAuthorityGetAllRequest?.all_leave?.length === 0)
      setShowingDataLoading(false);
    else {
    }
    if (latAuthorityGetAllRequest?.all_leave?.length === 10) setState(true);
    else setState(false);
  }, [latAuthorityGetAllRequest?.all_leave]);

  const onRefetchWhenAction = () => {
    setPage(1);
    setShowingDataLoading(true);
    latAuthorityGetAllRequestRefetch();
  };
  return (
    <>
      {leaveList?.map((leave, index) =>
        leaveList?.length === index + 1 ? (
          <div ref={ref} key={leave?._id}>
            <RecomendingLeaveRequestCard
              leave={leave}
              onStaffLeaveListRefetch={onRefetchWhenAction}
              modId={modId}
              flow="Recommend_Section"
            />
          </div>
        ) : (
          <RecomendingLeaveRequestCard
            key={index}
            leave={leave}
            onStaffLeaveListRefetch={onRefetchWhenAction}
            modId={modId}
            flow="Recommend_Section"
          />
        )
      )}

      {showingDataLoading && <QvipleLoading />}
    </>
  );
};

export default RecomendingLeaveRequest;
